.btn {
    width: 18.5rem;
    text-align: center;
    font-size: 1.6rem;
    color: $black;
    font-weight: 400;

    text-decoration: none;

    border: 1px solid $black;

    border-radius: 10rem;

    padding: 1.4rem 0;

    &--header {

        @include breakpoint($tablet) {
            display: none;
        }
    }

    &--header-top {
        margin-right: 2rem;
        background: $green;
        border: none;
        color: $white;

        @include breakpoint($tablet) {
            display: none;
        }
    }

    &--header-bot {
        width: 22.6rem;
        background: $yellow;

        border: none;

        @include breakpoint($tablet-bg) {
            order: 3;
        }
    }

    &--about {
        color: $white;
        width: 26rem;
        background: $green;

        border: none;

        @include breakpoint($tablet-bg) {
            display: none;
        }
    }

    &--study {
        @include breakpoint($mobile) {
            width: 100%;
        }
    }
}