.study {
    background:linear-gradient(264.23deg, rgba(49, 138, 42, 0.5) 1.64%, rgba(49, 138, 42, 0) 116.91%), url('../img/study/bg.png') center no-repeat;
    background-size: cover;

    margin-bottom: 7rem;

    @include breakpoint($mobile) {
        margin-bottom: 1.7rem;
    }
}

.study__inner {
    display: flex;
    align-items: center;

    @include breakpoint($laptop) {
        padding: 2rem 0;
    }

    @include breakpoint($tablet) {
        flex-direction: column;
    }
}

.study__content {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding-right: 2rem;

    @include breakpoint($tablet) {
        width: 100%;
        padding-right: 0;

        margin-bottom: 4rem;
    }

    @include breakpoint($mobile) {
        margin-bottom: 3rem;
    }
}

.study__text {
    position: relative;
    font-family: $oswald;
    font-size: 3.1rem;
    line-height: 4.5rem;
    color: $white;
    font-weight: 400;

    text-transform: uppercase;

    margin-bottom: 6rem;

    &:after {
        content: "";
        position: absolute;
        bottom: -3rem;
        left: 0;
        z-index: 1;
        width: 85%;
        height: 4px;
        background: $white;

        @include breakpoint($mobile) {
            width: 100%;
        }

    }

    @include breakpoint($tablet-bg) {
        font-size: 2.6rem;
    }

    @include breakpoint($mobile) {
        font-size: 2rem;
        line-height: 2.9rem;
    }
}

.study__span {
    font-size: 2.6rem;
    color: $white;
    font-weight: 400;

    margin-bottom: 8.5rem;

    @include breakpoint($laptop) {
        margin-bottom: 6rem;
    }

    @include breakpoint($tablet-bg) {
        font-size: 2.2rem;
    }

    @include breakpoint($tablet) {
        margin-bottom: 3.5rem;
    }

    @include breakpoint($mobile) {
        width: 80%;
        font-size: 1.6rem;
        line-height: 1.8rem;

        margin-bottom: 2.3rem;
    }
}

.study__photo {
    width: 50%;
    img {
        width: 100%;
    }

    @include breakpoint($tablet) {
        width: 100%;
    }
}