.process {
    margin-bottom: 5rem;
}

.process__title {
    font-family: $oswald;
    font-size: 4.2rem;
    color: $black;
    font-weight: 400;

    text-transform: uppercase;
    text-align: center;

    margin-bottom: 4.8rem;

    @include breakpoint($mobile) {
        font-size: 2.8rem;

        padding: 0 3rem;

        margin-bottom: 2.2rem;
    }
}

.process__inner {
    display: flex;

    @include breakpoint($tablet) {
        flex-direction: column;
    }
}

.process__local {
    width: 30%;

    background: #DCEFE7;
    padding: 3.1rem 2rem 0;
    margin-right: 2rem;

    @include breakpoint($tablet) {
        width: 100%;

        margin-bottom: 2rem;
        margin-right: 0;
    }

    @include breakpoint($mobile) {
        margin-bottom: 1.9rem;

        padding: 1.3rem 2.2rem 0;
    }
}

.process__local-title {
    font-family: $oswald;
    font-size: 2.8rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 1.5rem;

    @include breakpoint($mobile) {
        font-size: 2rem;
        text-align: center;
        line-height: 2.9rem;

        margin-bottom: 2rem;
    }
}

.process__local-photo {

    img {
        width: 100%;
        border-radius: 1rem;
    }
}

.process__local-icon {
    position: relative;
    left: 50%;
    bottom: 3.5rem;
    z-index: 1;

    background: $white;

    width: 5.2rem;
    height: 5.2rem;
    border-radius: 50%;

    transform: translateX(-50%);

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 1;

        transform: translate(-50%, -50%);
    }

    box-shadow: 0 4px 20px rgba($black, 0.25);

    cursor: pointer;
}

.process__distance {
    width: 70%;

    background: #EDEDED;
    padding: 3.1rem 2rem 0;

    @include breakpoint($laptop) {
        padding: 3.1rem 2rem 1.5rem;
    }

    @include breakpoint($tablet) {
        width: 100%;
    }

    @include breakpoint($mobile) {
        padding: 1.4rem 1.1rem 2.5rem;
    }
}

.process__distance-title {
    font-family: $oswald;
    font-size: 2.8rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 2.2rem;

    @include breakpoint($mobile) {
        font-size: 2rem;
        text-align: center;

        margin-bottom: 3rem;
    }
}

.process__distance-inner {
    display: flex;

    @include breakpoint($tablet) {
        flex-direction: column;
    }
}

.process__distance-item {
    padding: 0 1.8rem;

    &:first-child {
        padding: 0;
        padding-right: 1.8rem;

        @include breakpoint($tablet) {
            padding-right: 0;
        }
    }

    &:last-child {
        padding: 0;
        padding-left: 1.8rem;

        @include breakpoint($tablet) {
            padding-left: 0;
        }
    }

    @include breakpoint($tablet) {
        display: flex;
        padding: 0;

        margin-bottom: 3.5rem;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.process__distance-img {
    width: 10rem;

    img {

        width: 100%;

        @include breakpoint($tablet) {
            width: 7rem;
        }

        @include breakpoint($mobile) {
            width: 4.6rem;
        }
    }
    margin-bottom: 2.4rem;

    @include breakpoint($tablet) {
        width: 25%;

        margin-bottom: 0;
    }
}

.process__distance-text {
    font-size: 1.8rem;
    color: $black;
    font-weight: 400;

    line-height: 2.1rem;

    @include breakpoint($tablet) {
        width: 75%;
    }

    @include breakpoint($mobile) {
        font-size: 1.2rem;
        line-height: 1.2rem;
    }
}