.video {
    padding-bottom: 11.3rem;
    margin-bottom: 3rem;

    position: relative;

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 4px;
        height: 80px;
        background: #EDEDED;

        @include breakpoint($mobile) {
            height: 40px;
        }
    }

    @include breakpoint($mobile) {
        padding-bottom: 5.7rem;
        margin-bottom: 1.8rem;
    }
}

.video__inner {
    display: flex;

    background: url('../img/video/bg.png') left -216px center no-repeat, #EDEDED;
    padding: 3.3rem 3rem 3.3rem 5.6rem;

    @include breakpoint($tablet-bg) {
        align-items: center;
    }

    @include breakpoint($tablet) {
        flex-direction: column;
        padding: 3.3rem 3rem;
    }

    @include breakpoint($mobile) {
        background: #EDEDED;

        padding: 1.2rem 1.6rem 1.7rem;
    }
}

.video__title {
    width: 40%;
    font-family: $oswald;
    font-size: 4.2rem;
    line-height: 6.2rem;
    color: $black;
    font-weight: 400;

    padding-right: 15rem;

    @include breakpoint($tablet-bg) {
        font-size: 3.5rem;

        padding-right: 0;
        margin-right: 5rem;
    }

    @include breakpoint($tablet) {
        width: 100%;
        margin-right: 0;

        text-align: center;
        margin-bottom: 2.5rem;
    }

    @include breakpoint($mobile) {
        font-size: 2.8rem;
        line-height: 4.1rem;
    }
}

.video__player {
    width: 60%;

    cursor: pointer;

    img {
        width: 100%;
    }

    @include breakpoint($tablet) {
        width: 100%;
    }
}