.nav {
    display: flex;

    @include breakpoint($laptop) {
        flex-wrap: wrap;

        align-items: center;
    }

    @include breakpoint($tablet-bg) {
        width: 100%;
        order: 1;

        margin-bottom: 2rem;
    }

    @include breakpoint($tablet) {
        flex-direction: column;

        padding-top: 5rem;
        position: relative;
    } 

}

.nav__item {
    font-size: 1.9rem;
    color: $white;
    font-weight: 400;

    a {
        color: inherit;
        text-decoration: none;
    }

    text-transform: uppercase;

    padding: 0 1.7rem;

    @include breakpoint($laptop) {
        width: 33.3333%;
        font-size: 1.7rem;

        padding: 0 1.5rem;
        margin-bottom: 1rem;
    }

    @include breakpoint($tablet-bg) {
        text-align: center;

        margin-bottom: 1rem;
    }

    @include breakpoint($tablet) {
        color: #0e0e0e;
        width: 100%;
        font-size: 2.5rem;

        margin-bottom: 3rem;
    }

    @include breakpoint($mobile) {
        font-size: 2rem;
        margin-bottom: 2.5rem;
    }

    &-mob {
        display: none;

        @include breakpoint($tablet) {
            display: block;
        }
    }

    &--email {
        text-transform: none;
        font-size: 1.6rem;
        color: #0b552a;
        text-decoration: underline;

        a {
            color: #0b552a;
        }
    }

    &--metro {
        display: flex;
        justify-content: center;

        @include breakpoint($tablet) {
            display: flex;
        }
    }

    &--address {
        text-transform: none;
        font-size: 1.6rem;
        color: $black;
        
        address {
            font-style: normal;
        }
    }
}

.nav__item--metro-item {
    display: none;
    align-items: center;

    margin-right: 1rem;

    img {
        margin-right: 0.5rem;
    }

    span {
        text-transform: none;
        font-size: 1.6rem;
        color: $black;
    }

    &:last-child {
        margin-right: 0;
    }

    @include breakpoint($tablet) {
        display: flex;
    }
}

.nav__close {
    display: none;
    position: absolute;
    top: 1rem;
    right: 1rem;

    img {
        width: 2.5rem;
        height: 2.5rem;
        filter: invert(0%) sepia(0%) saturate(133%) hue-rotate(178deg) brightness(93%) contrast(89%);
    }

    @include breakpoint($tablet) {
        display: block;
    }
}