.header__top-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2.3rem 0;

    @include breakpoint($laptop) {
        flex-wrap: wrap;
    }

    @include breakpoint($mobile) {
        padding: 2rem 0;
    }

}

.header__logo {

    @include breakpoint($laptop) {
        width: 50%;
    }

    @include breakpoint($tablet) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        order: 4;
    }

    .header__logo-img {
        @include breakpoint(450px) {
            width: 80%;
        }
    }
}

.header__center {
    display: flex;
    align-items: center;

    @include breakpoint($laptop) {
        width: 50%;
    }
}

.header__address {
    font-size: 1.7rem;
    color: #0E0E0E;
    font-weight: 400;

    font-style: normal;
    margin-right: 2rem;

    @include breakpoint($tablet) {
        display: none;
    }
}

.header__contact {
    display: flex;
    flex-direction: column;

    @include breakpoint($laptop) {
        width: 100%;
        flex-direction: row;
        justify-content: space-between;

        padding-top: 2rem;
    }

    @include breakpoint($tablet) {
        padding-top: 0;
        margin-bottom: 1.5rem;
    }

    @include breakpoint($mobile) {
        margin-bottom: 1.4rem;

    }

    position: relative;
    &:after {
        content: "";
        display: none;
        position: absolute;
        bottom: -0.7rem;
        left: -20%;
        z-index: 1;
        width: 140%;
        height: 1px;
        background: rgba($black, 0.2);

        @include breakpoint($mobile) {
            display: block;
        }
    }
}

.header__time {
    display: flex;

    img {
        margin-right: 0.5rem;

        @include breakpoint($laptop) {
            margin-right: 1rem;
        }
    }

    span {
        font-size: 1.6rem;
        color: #0E0E0E;
        font-weight: 400;
    }

    margin-bottom: 0.5rem;

    @include breakpoint($laptop) {
        align-items: center;

        margin-bottom: 0;
    }

    @include breakpoint($tablet) {
        display: none;
    }
}

.header__backcall {
    display: flex;

    img {
        margin-right: 1.1rem;
    }

    span {
        font-size: 1.6rem;
        color: #0E0E0E;
        font-weight: 400;
    }

    @include breakpoint($tablet) {
        display: none;
    }
}

.header__num {
    font-size: 3.1rem;
    color: #0E0E0E;
    font-weight: 400;

    a {
        text-decoration: none;
        color: inherit;
    }

    margin-bottom: 0.5rem;

    img {
        display: none;
        margin-right: 1rem;

        @include breakpoint($tablet) {
            display: block;
        }
    }

    @include breakpoint($tablet) {
        display: flex;
        font-size: 2.1rem;

        order: 1;

        margin-right: 2rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.7rem;
    }

    position: relative;

    // &:after {
    //     content: "";
    //     display: none;
    //     position: absolute;
    //     top: 0;
    //     right: -2.5rem;
    //     z-index: 1;
    //     width: 1px;
    //     height: 2.4rem;
    //     background: rgba($black, 0.2);

    //     @include breakpoint($mobile) {
    //         display: block;
    //     }
    // }

}

.header__loupe {
    display: none;
    order: 2;

    @include breakpoint($tablet) {
        display: block;
    }

    @include breakpoint($mobile) {
        img {
            width: 2rem;
        }
    }

    position: relative;

    // &:after {
    //     content: "";
    //     display: none;
    //     position: absolute;
    //     top: 0;
    //     right: -2rem;
    //     z-index: 1;
    //     width: 1px;
    //     height: 2.4rem;
    //     background: rgba($black, 0.2);

    //     @include breakpoint($mobile) {
    //         display: block;
    //     }
    // }
}

.header__langs {
    display: none;

    order: 3;

    @include breakpoint($tablet) {
        display: flex;
    }
}

.header__langs-item {
    font-size: 2rem;
    color: $black;
    font-weight: 400;

    a {
        color: inherit;
        text-decoration: none;
    }

    padding: 0 0.5rem;

    &.active {
        font-weight: 500;
    }

    @include breakpoint($mobile) {
        font-size: 1.7rem;
    }
}

.header__bottom {
    background: #0E0E0E;

    @include breakpoint($tablet) {
        display: none;

    }

    &.active {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 10000;
        display: block;
        width: 100%;
        height: 100vh;
        background: #ededed;

        .header__home {
            display: none;
        }

        .btn--header-bot {
            display: none;
        }

        .nav__item-mob {
            .btn--header-bot {
                text-transform: none;
                display: block;

                margin: 0 auto;
            }

            .btn--header-top {
                width: 22.6rem;
                display: block;
                color: $white;
                text-transform: none;
                margin: 0 auto;
            }

            .btn--header {
                width: 22.6rem;
                display: block;
                color: $black;
                margin: 0 auto;
                border: none;
                text-transform: none;

                background: $white;
            }
        }

        .header__search {
            display: none;
        }
    }
}

.header__bottom-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 0;

    @include breakpoint($tablet-bg) {
        flex-wrap: wrap;
    }
}

.header__home {

    @include breakpoint($tablet-bg) {
        width: 33.3333%;
        order: 2;
    }
}

.header__search {

    input {
        font-size: 1.8rem;
        color: $black;
        font-weight: 400;
        border-radius: 10rem;

        padding: 2rem 0 1rem 2.4rem;

        &::placeholder {
            color: rgba($black, 0.5);
        }

        &:focus {
            outline: none;
        }

    }

    @include breakpoint($tablet-bg) {
        order: 4;
        width: 33.3333%;
    }

}